<template>
  <Row class="vm-table vm-panel">
    <div class="panel-body">
      <Row type="flex" justify="space-between" class="control">
        <div class="table-style">
          <h3>表格样式</h3>
          <i-switch v-model="showStripe" style="margin: 0 30px 0 10px"></i-switch>
          <!--<h3>Size</h3>-->
          <Radio-group v-model="tableSize" type="button" style="margin-left: 10px">
            <Radio label="large">大</Radio>
            <Radio label="medium">中</Radio>
            <Radio label="small">小</Radio>
          </Radio-group>
        </div>
        <div class="search-bar">
          <Input placeholder="在列表中搜索Api..." v-model="keyword" style="width: 300px" @on-change="search"></Input>
          <Button type="ghost"><i class="fa fa-search"></i></Button>
        </div>  
      </Row>
      <div class="edit" v-if="type === 'edit'">
          <Button @click="modalAdd = true" ><i class="fa fa-plus"></i> Add</Button>
          <Button  :disabled="deleteDisabled" @click="modalDelete = true"><i class="fa fa-trash"></i> Delete</Button>
      </div>
      <Table :stripe="showStripe" :size="tableSize" :columns="showColumns" :data="dataShow" @on-selection-change="selectChange" highlight-row="true"></Table>
      <Row type="flex" justify="space-between" class="footer">
        <div class="info-bar">
          第<Input-number class="input-number" v-model="showNum" :max="data.length" :min="1" @on-change=" updateDataShow ">{{ showNum }}</Input-number>/ 页
        </div>
        <div class="page">
          <span class="total">共 {{ data.length }} 条</span>
          <Page :total="data.length" :current="currentPage" :page-size="showNum" @on-change="pageChange"></Page>
        </div>
      </Row>
    </div>
    <Modal
        v-model="modalEdit"
        title="编辑Api"
        ok-text="确定"
        cancel-text="取消"
        v-on:on-ok="editOk">
        <Form :label-width="80">
          <Form-item v-if="key=='api_name' " v-for="(value, key) in dataEdit" :label="convertKey(key)" :key="dataEdit.id">
            <Input v-model="dataEdit[key]" :placeholder="key" style="width:90%;" readonly></Input>
          </Form-item>

           <Form-item v-if="key=='upstream_value'" v-for="(value, key) in dataEdit" :label="convertKey(key)" :key="dataEdit.id">
            <Input v-model="dataEdit[key]" :placeholder="key" style="width:90%;"></Input>
          </Form-item>

          <Form-item v-if="key=='method' " v-for="(value, key) in dataEdit" :label="convertKey(key)" :key="dataEdit.id">
            <Select v-model="dataEdit[key]" size="large" placeholder="请选择" style="width:70%;">
                    <Option value="GET">GET</Option>
                    <Option value="POST">POST</Option>
              </Select>
          </Form-item>

           <Form-item v-if="key=='upstream_mode' " v-for="(value, key) in dataEdit" :label="convertKey(key)" :key="dataEdit.id">
            <Select v-model="dataEdit[key]" size="large" placeholder="请选择" style="width:70%;">
                     <Option value="1">直接寻址</Option>
                    <Option value="2">ETCD寻址</Option>
              </Select>
          </Form-item>
        </Form>
    </Modal>
    <Modal
        width='480'
        v-model="modalAdd"
        title="创建Api"
        ok-text="确定"
        cancel-text="取消"
        v-on:on-ok="addOk" >
        <Form :label-width="80">
          <Form-item v-if="item.key=='api_name' || item.key=='upstream_value'" v-for="item in columns" :label="item.title" :key="item.id">
                <Input  v-model="dataAdd[item.key]" :placeholder="'请输入' + item.title" style="width:70%;"></Input>
          </Form-item>
          <Form-item v-if="item.key=='method'" v-for="item in columns" :label="item.title" :key="item.id">
              <Select v-model="dataAdd[item.key]" size="large" placeholder="请选择" style="width:70%;">
                    <Option value="GET">GET</Option>
                    <Option value="POST">POST</Option>
              </Select>
            </Form-item>

            <Form-item v-if="item.key=='upstream_mode'" v-for="item in columns" :label="item.title" :key="item.id">
              <Select v-model="dataAdd[item.key]" size="large" placeholder="请选择" style="width:70%;">
                     <Option value='1'>直接寻址</Option>
                    <Option value='2'>ETCD寻址</Option>
              </Select>
            </Form-item>
        </Form>
    </Modal>

    <Modal
    width="300"
    style="text-align:center"
        v-model="modalDelete">
  
        <p slot="header" style="color:#f60;text-align:center">
            <Icon type="information-circled"></Icon>
            <span>删除Api</span>
        </p>
        <div style="text-align:center">
            <h3>不可恢复</h3>
            <p style="margin-top:6px;margin-left:8px">仍然继续删除？</p>
        </div>
        <div slot="footer">
            <Button type="error" size="large"  :loading="modal_loading" @click="deleteOk" long>删除</Button>
        </div>
    </Modal>
  </Row>
</template>

<script>
 import axios from 'axios'
  export default {
    name: 'VmTable',
    props: {
      title: {
        type: String,
        default: 'Basic Table'
      },
      type: String,
      columns: Array,
      data: Array
    },
    data () {
      return {
        deleteDisabled: true,
        dataShow: [],
        showNum: 8,
        showStripe: true,
        tableSize: 'medium',
        currentPage: 1,
        keyword: '',
        modalEdit: false,
        modalAdd: false,
        modalDelete: false,
        dataEdit: {},
        dataDelete: [],
        dataAdd: {},
        formData: []
      }
    },
    methods: {
      editOk: function () {
          var par = this
  
        axios({
            method: 'post',
            url: '/api/update',
            params: par.dataEdit
        }).then(function(res) { 
            console.log(res)
            if (res.data.suc == true) { 
                    par.$Message.success('更新成功');
                     // 更新列表
                    par.$emit('edit-ok', par.dataEdit)
            } else {
                    par.$Message.error({
                        content: res.data.data.msg,
                        duration: 2
                    });
                    par.modalEdit = true
                }
        });

       
      },
      addOk: function () {
        var par = this
  
        axios({
            method: 'post',
            url: '/api/create',
            params: par.dataAdd
        }).then(function(res) { 
            console.log(res)
            if (res.data.suc == true) { 
                     par.$Message.success('创建成功');
                     // 更新列表
                    par.$emit('add-ok', par.dataAdd)
            } else {
                    par.$Message.error({
                        content: res.data.data.msg,
                        duration: 2
                    });
                    par.modalAdd = true
                }
        });
      },
      deleteOk: function () {
        this.$emit('delete-ok', this.dataDelete)
        this.modal_loading = true;
                setTimeout(() => {
                    this.modal_loading = false;
                    this.modalDelete = false;
                    this.$Message.success('删除成功');
                }, 200);
      },
      pageChange: function (page) {
        this.currentPage = page
        this.updateDataShow()
      },
      updateDataShow: function () {
        let startPage = (this.currentPage - 1) * this.showNum
        let endPage = startPage + this.showNum
        this.dataShow = this.data.slice(startPage, endPage)
      },
      search: function () {
        let that = this
        let tempData = that.data
        that.dataShow = []
        tempData.forEach(function (elem) {
          for (let i in elem) {
            if (elem[i].toString().indexOf(that.keyword) > -1) {
              that.dataShow.push(elem)
              return
            }
          }
        })
      },
      selectChange: function (data) {
        this.dataDelete = data
      },
      remove: function (index) {
        this.dataShow.splice(index, 1)
      },
      renderOperate: function (h, params) {
        return h('div', [
          h('Button', {
            props: {
              type: 'info',
              size: 'small'
            },
            style: {
              marginRight: '5px',
              backgroundColor: '#37cbc1',
              borderColor: '#37cbc1',
              width: '3rem'
            },
            on: {
              click: () => {
                for (let i in params.row) {
                  this.dataEdit[i] = params.row[i]
                }
                delete this.dataEdit._index
                this.modalEdit = true
              }
            }
          }, '编辑'),
          h('Button', {
            props: {
              type: 'error',
              size: 'small'
            },
            style: {
                width: '3rem'
            },
            on: {
              click: () => {
                this.dataDelete.push(params.row)
                this.modalDelete = true
              }
            }
          }, '删除')
        ])
      },
      convertKey: function (value) {
        let returnValue = value
        this.columns.forEach(function (elem) {
          for (let i in elem) {
            if (i === 'key' && elem[i] === value) {
              returnValue = elem.title
            }
          }
        })
        return returnValue
      }
    },
    computed: {
      showColumns: function () {
        let showColumn = this.columns.slice()
        showColumn.forEach(function (elem) {
          elem.sortable = true
        })
        if (this.type === 'edit') {
          showColumn.unshift({
            type: 'selection',
            width: 60,
            align: 'center'
          })
          showColumn.push({
            title: '操作',
            key: 'action',
            width: 150,
            align: 'center',
            render: this.renderOperate
          })
        }
        return showColumn
      }
    },
    watch: {
      data: function () {
        this.dataShow = this.data.slice(0, this.showNum)
      },
      dataDelete: function () {
        if (this.dataDelete.length === 0) {
          this.deleteDisabled = true
        } else {
          this.deleteDisabled = false
        }
      }
    },
    mounted: function () {
      this.dataShow = this.data.slice(0, this.showNum)
    }
  }
</script>



