<template>
  <VmTable title="Api列表" 
           type="edit" 
           :columns="dataColumns" 
           :data="dataTable"
           v-on:add-ok="add"
           v-on:edit-ok="edit"
           v-on:delete-ok="deletefn"
           class="vm-margin">
  </VmTable>
</template>


<script>
  import axios from 'axios'
  import VmTable from '@/pages/openapi/component/ApiListTable'
  export default {
    name: 'ApiList',
    components: {
      VmTable
    },
    methods: {
      add: function (data) {
        this.dataTable.unshift(data)
      },
      edit: function (data) {
        this.dataTable.forEach(function (elem) {
          if (elem.id === data.id) {
            for (let i in data) {
              elem[i] = data[i]
            }
          }
        })
      },
      deletefn: function (data) {
        var api_names = ""
        for (let i = 0; i < this.dataTable.length; i++) {
          for (let j = 0; j < data.length; j++) {
            if (this.dataTable[i].id === data[j].id) {
              this.dataTable.splice(i, 1)
              api_names = api_names + "," + data[j].api_name
            }
          }
        }

        var par = this
        axios({
            method: 'post',
            url: process.env.OPENAPI_ADMIN_ADDR  + '/api/delete',
            params: {
                apis: api_names
            }
        }).then(function(res) { 
            console.log(res)
            if (res.data.suc == true) { 
                    par.dataTable = res.data.data.apis
            } else {
                par.$Message.error(res.data.data.msg);
            }
        });
      }
    },
    data () {
      return {
        dataColumns: [
          {
            id: '20156541',
            title: 'Api名',
            key: 'api_name'
          },
          {
            id: '20156542',
            title: 'Method',
            key: 'method',
            width:200
          },
          {
            id: '20156543',
            title: '寻址模式',
            key: 'upstream_mode',
            width:200
          },
          {
            id: '20156544',
            title: '目标地址',
            key: 'upstream_value'
          }
        //    {
        //     id: '20156545',
        //     title: '公司',
        //     key: 'company',
        //     width:120
        //   },
        //    {
        //     id: '2015656',
        //     title: '产品',
        //     key: 'product',
        //     width:120
        //   },
        //    {
        //     id: '20156547',
        //     title: '系统',
        //     key: 'system',
        //     width:120
        //   },
        //    {
        //     id: '20156548',
        //     title: '接口',
        //     key: 'interface',
        //     width:120
        //   },
        //   {
        //     id: '20156549',
        //     title: '版本号',
        //     key: 'version',
        //     width:120
        //   },
        ],
        dataTable: []
      }
    },
    created() {
        // 从服务器加载数据，并保存到dataTable中
        var par = this

    
        axios({
            method: 'get',
            url: process.env.OPENAPI_ADMIN_ADDR  + '/api/list'
        }).then(function(res) { 
            console.log(res)
            if (res.data.suc == true) { 
                    par.dataTable = res.data.data.apis
            } else {
                par.$Message.error(res.data.data.msg);
                }
        });
    }
  }
</script>


